.header{
    background-color: white;
    position: sticky;
    top:0;
    display: flex;
    justify-content: space-evenly;
    border: 0.1px lightgray solid;
    padding: 10px 10px;
    width: 100%;
    z-index: 999;
    box-shadow: 0 3px 10px rgb(0 0 0 / 0.2);
    overflow-x: hidden;
    
}
 .header__left{
    display: flex;
 }

.header__left > img{
    object-fit: contain;
    height: 40px;
    margin-right: 10px;
}


.header__search{
    padding: 10px;
    display: flex;
    align-items: center;
    border-radius: 5px;
    height:22px;
    color: grey;
    background-color: #eef3f8;
}

.header__search > input {

    outline: none;
    border: none;
    background: none;

}


.header__right{
    display: flex;
    gap: 12px;
   
    

  
}


.header__left img{
    cursor: pointer;
}