

.parentlogin{
    display: flex;
    background-image: url('loginbackground3.jpg');
    background-size: cover;
    height: 100vh;
   
}
.login{
    display: grid;
    place-items: center;
    margin-left: auto;
    margin-right: auto;
    padding-top: 100px;
    padding-bottom: 100px;
 
}

.login >p{
    color: white;
}



.login >img{
    object-fit: contain;
    height: 70px;
    margin-top: 20px;
    margin-bottom: 20px;
}


.login > form{
    display: flex;
    flex-direction: column;

}

.login >form>input{
    width: 350px;
    height: 50px;
    font-size:20px ;
    padding-left: 10px;
    margin-bottom: 10px;
    border-radius: 5px;
    color: white;
    background: transparent;
    backdrop-filter: blur(4px);
    border: 1px solid lightblue;
    outline: none;
}

.login > form>button{
     width: 365px;
    height: 50px;
    font-size:large ;
    padding-left: 10px;
    margin-bottom: 10px;
    background-color: #0074b1;
    color: white;
    border-radius: 5px;
    cursor: pointer;

}


.login__register{
    color: cyan ;
    cursor: pointer;
}

.login>p{
    margin-top: 10px;

}

.login .form >input{
    background: transparent;
    border: 0.1px solid lightgrey;
    outline: none;
    color: white;
}