.feed{
    flex: 0.6;
    margin: 0px 20px;
}


.feed__inputContainer{
   background-color: white;
   padding: 10px;
   padding-bottom: 20px;
   border-radius: 10px;
   margin-bottom: 20px;
}

.feed__input{
    border: 1px solid lightgrey;
    display: flex;
    padding: 10px;
    border-radius: 30px;
    padding-left: 25px;
    color: gray;
}


.feed__input >form{
    display: flex;
    width: 100%;
}


.feed__input >form>input{
    flex: 1;
    border: none;
    margin-left: 10px;
    outline-width: 0;
    font-weight: 600;
   
    

}

.feed__input >form>button{
    display: none;
}


.feed__inputOptions{
    display: flex;
    justify-content: space-evenly;
}